.#{$namespace}-wrapper {
  .rotate {
    transform: rotate(180deg);
    transform-origin: center;
    transition: transform .6s ease-out;
  }
  
  // Toggle showing and hiding panel area
  [data-show="true"] {
    transform: none;
    margin-top: px-to-rem(12px);
    max-height: px-to-rem(1104px);
    transition: max-height .6s ease-out, margin .6s ease;
  
    .#{$namespace}-divider,
    .#{$namespace}-btn,
    .form_field {
      opacity: 1;
      transition: opacity .7s ease-out;
    }
    .input-grid {
      max-height: px-to-rem(1104px);
      transition: max-height .6s ease-out;
    }
  }
  
  [data-show="false"] {
    max-height: 0;
    margin: 0;
    padding: 0!important;
    overflow: hidden;
    transition: max-height .6s ease-out, margin .6s ease;
    .input-grid {
      max-height: 0;
      transition: max-height .6s ease-out;
    }
    .#{$namespace}-divider,
    .#{$namespace}-btn,
    .form_field
     {
      opacity: 0;
      transition: opacity .1s ease-out;
    }
  }
}