.#{$namespace}-lbl {
  display: inline-block;
  font-size: px-to-rem(16px);
  line-height: px-to-rem(20px);
  margin-right: px-to-rem(3px);
  position: relative;
  vertical-align: middle;
  white-space: nowrap;
  padding-top: px-to-rem(1px);

  &:after {
    border-bottom: px-to-rem(1px) solid transparent;
    bottom: 0;
    content: '';
    display: block;
    left: 0;
    position: absolute;
    transition: border $transition-duration $transition-func;
    width: 100%;
    
  }
}